		elementorFrontend.hooks.addAction( 'frontend/element_ready/ld_single_portfolio_related.default', lqdPfSingleRelatedHandler );
		elementorFrontend.hooks.addAction( 'frontend/element_ready/ld_masked_image.default', lqdMaskedImageHandler );
		elementorFrontend.hooks.addAction( 'frontend/element_ready/ld_particles.default', lqdParticlesHandler );
		elementorFrontend.hooks.addAction( 'frontend/element_ready/ld_team_member.default', lqdTeamMemberHandler );
